import React, { useState,useEffect } from 'react';
import { Button, Gallery, Root, Separator,ScreenSpinner } from '@vkontakte/vkui';

import icon from '../img/icon_150.png';
import '../css/FirstConnect.css'

const createPerson = (props,selected) =>{
    
    var string_array = "";
    for (var i=0; i<selected.length; ++i) {
        string_array = string_array + '&categori[]=' + selected[i];
    }
    // console.log(string_array)
    fetch(`https://task-today-tpuhack.herokuapp.com/create_employee?id=id${props.id_v}&phone=892346750213&about=""${string_array}&url=${props.sign}`)
    		.then(response => response.json())
    		.then(result =>{
        	try{
                // props.setPopout(null);
				if (result == "1") {
                    alert("Возникла ошибка, возможно идут технические работы, попробуйте позже")
                    
					// props.setActiveStory('Task')
				}else{
                    // alert("Возникла ошибка, возможно идут технические работы, попробуйте позже")
                    window.location.reload();
                    // props.setActiveStory('FirstConnect')
                }
                // window.location.reload();
                // props.setActiveStory('Task')
                // console.log("GO HOME"); 
        }catch(err){ 
            alert("Возникла ошибка, возможно идут технические работы, попробуйте позже"); 
            console.log(err);
        }

        }).catch(err => {
            alert("Возникла ошибка, возможно идут технические работы, попробуйте позже"); 
            console.log(err)
        })

}

const FirstConnect = (props) => {        

    var selected =[];
    
    const thematics = [
        {id: 1, name: "Программирование"},
        {id: 2, name: "Доставка"},
        {id: 3, name: "Учёба"},
        {id: 4, name: "Рисование"},
        {id: 5, name: "Дизайн"},
        {id: 6, name: "Пение"},
        {id: 7, name: "Готовка"},
        {id: 8, name: "Minecraft"},
        {id: 9, name: "Общение"},
        {id: 10, name: "Прочее"},
        
      ];
    props.setPopout(null);
    const [search, SetSearch] = useState("");
    const [slideIndex, setSlideIndex] = useState(0);

    useEffect(() => {
        setTimeout(()=>setSlideIndex(1),1000);
        
    }, [])
    
    const onChange = (e) =>
    {
        SetSearch(e.target.value);        
    }   
    const chooseClick = (name) => {
        var element = document.getElementById(name);
        switch (element.getAttribute('class')){
            case 'CardCategorySelectedFirstConnect': 
                element.setAttribute('class', 'CardCategoryUnselectedFirstConnect');
                selected.splice(selected.indexOf(name),1);
            break;
            case 'CardCategoryUnselectedFirstConnect': 
                element.setAttribute('class', 'CardCategorySelectedFirstConnect');
                selected.push(name);
            break;
        }
        document.getElementById("buttonGo").setAttribute('style',selected.length != 0?"background : #4bb34b":"background : #e64646")
        // console.log(document.getElementById("buttonGo"));
      }
    
    return(
        <Root id={props.id} >
            {/* <div>Hello</div> */}
        {/* <Panel> */}
        
		    {/* <PanelHeader className='PanelHeader'>TaskToday</PanelHeader>*/}
            <Gallery className="GalleryBodyFirstConnect" bullets="dark" align='center'  disabled slideIndex={slideIndex} onChange={(s)=>{setSlideIndex(1)}} slideWidth="100%">
                <div key="Page1" className="Page1FirstConnect">
                    <div className="GalleryBodyCenterPage1">
                        <img className="IconFirstConnect" src={icon}  />
                        <div className="TextFirstConnect">TaskToday</div>
                        <div className="Text2FirstConnect">Работа у вас под рукой</div>
                    </div>
                    
                </div>
                <div key="Page2" className="Page2FirstConnect">
                            {/* <div>Добро пожаловать</div> */}
                    <div className="GalleryBodyCenterPage2">
		            <div className="Text3FirstConnect">Покажи на что способен</div> 
                    <Separator />
                    <div className="CategoryFirstConnect">
                        {thematics.map((category,index) => {
                            return(<div className="CardCategoryUnselectedFirstConnect" id={category.name}  onClick={()=>{
                                // console.log("Click");
                                // document.getElementById("buttonGo").setAttribute('disabled',selected.length != 0);
                                chooseClick(category.name);
                            }} key={index}>{category.name}</div>)
                        })  
                        }
                    </div>
                    <Separator />
                    <div className="ButtonFirstConnect">
                        <Button size="xl" mode="destructive" id="buttonGo"  onClick={()=>{
                                    if (selected.length != 0) {
                                        props.setPopout(<ScreenSpinner size='large' />);
                                        createPerson(props,selected);
                                    }
                                    
                                    // createPerson(props,selected)
                        }}>Начать использование</Button> 
                    </div>
                    </div>
                </div>
                
            
                </Gallery>
        
        
        
	        {/* </Panel> */}
        </Root>
    );
	
};

FirstConnect.propTypes = {
	
};

export default FirstConnect;
